import React, { useState } from "react";
import { Modal, Button } from "antd";
import { FilePdfOutlined } from "@ant-design/icons";
import Satzung from "../Satzung/Satzung";

const SatzungButton: React.FC<{ locale: "EN" | "DE" }> = ({locale}) => {
  const [isVisible, setIsVisible] = useState(false);

  const setModalVisibility = (value: boolean) => () => {
    setIsVisible(value);
  };

  return (
    <div className="modal">
      <Button type="primary" onClick={setModalVisibility(true)} icon={<FilePdfOutlined />}>
        {`Satzung (${locale})`}
      </Button>
      <Modal title="Satzung" visible={isVisible} footer={null} onCancel={setModalVisibility(false)}>
        <Satzung locale={locale} />
      </Modal>
    </div>
  );
};
export default SatzungButton;
